import React , { useState , useEffect }  from "react";
import stl from '../styles/movie-single.module.css'
import axios from 'axios'
import { APIkey } from '../helpers'
import { Link } from "react-router-dom";
import CAST from '../components/CAST/index'
import REVIEWS from '../components/REVIEWS/index'

export default ({ match ,  location }) => {
    const [ movie , setMovie ] = useState(null)

    useEffect(() => {
        const { id } = match.params
        async function fetchData() {
            const { data } = await axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=${APIkey}`)
            setMovie(data)
          }
        fetchData()
    }, [])


 const { id , backdrop_path , title , vote_average , overview , genres} = movie ? movie : ''

    return (
        <div>
        {movie && 
        <>
            <div className = {stl.wrapper}>
               <img alt = {'image-movie'} src = {`https://image.tmdb.org/t/p/original${backdrop_path}`} />
               <div>
                   <h3>{title}</h3>
                   <h4>Overview</h4>
                   <p>{overview}</p>
                   <h4>Jenres</h4>
                   {genres.map(({ id , name }) => <span key = {id}>{name}</span>)}
                   <h4>SCORE</h4>
                   <p>{vote_average}</p>
               </div>
            </div>
            <h2 className = {stl.additional_Info}>Additional information</h2>
            <div className = {stl.addInfoWrapper}>
                <span><Link to = {`/movies/${id}/cast`}>CAST</Link></span>
                <span><Link to = {`/movies/${id}/reviews`}>REVIEWS</Link></span>
            </div>
            </>
        }
         {location.pathname.includes('cast') && <CAST APIkey = {APIkey} id = {id} /> }
         {location.pathname.includes('reviews') && <REVIEWS APIkey = {APIkey} id = {id} />}
     </div>
    )
}