import React , { useState } from "react";
import stl from '../styles/movie-search.module.css'
import axios from 'axios'
import { Link } from "react-router-dom";
import { APIkey } from '../helpers'


export default () => {

    const [ movies , setMovies ] = useState(null)

    const handleSearch = async e => {
        if(e.target.value !== null && e.target.value.trim() !== ''){
            const query = e.target.value
            const { data } = await axios.get(`https://api.themoviedb.org/3/search/movie?api_key=${APIkey}&query=${query}`)
            data.results.length ? setMovies(data.results) : setMovies(null)
        }else setMovies(null)
    }


    return (
        <div className = {stl.wrapper}>
          <input placeholder = 'search keyword' onChange = {(e) => handleSearch(e)} />
          {movies  && 
           <ul>
            {movies.map(({ id , title }) => (
              <li key = {id}><Link to = {`movies/${id}`}>{title}</Link></li>
            ))}
          </ul>}
        </div>

    )
}